<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div v-if="!showList">
      
    <!-- 
            MODAL CONSULTA DE CLIENTE POR NOME
         -->
    <b-modal
      id="modal"
      v-model="modalShow"
      size="lg"
      title="Clientes"
      no-close-on-backdrop
      no-close-on-esc
      header-bg-variant="success"
      ok-only
      ok-variant="info"
      ok-title="Sair"
    >
      <b-container fluid>
        <b-row>
          <b-col>
            <b-card title="Buscar">
              <b-row>
                <b-col sm="11">
                  <b-form-input
                    v-model="searchModal"
                    type="text"
                    @keypress.enter="buscarClientePorNome"
                  ></b-form-input>
                </b-col>
                <b-col sm="1">
                  <b-button
                    pill
                    variant="success"
                    size="sm"
                    @click="buscarClientePorNome"
                  >
                    <b-icon-search></b-icon-search>
                  </b-button>
                </b-col>
              </b-row>
            </b-card>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <b-card title="Resultados">
              <b-overlay :show="loading" spinner-variant="primary" rounded="sm">
                <b-table
                  stacked="sm"
                  label-sort-asc=""
                  label-sort-desc=""
                  label-sort-clear=""
                  dense
                  striped
                  hover
                  :items="buscado"
                  primary-key="id"
                  small
                  sticky-header
                  head-variant="dark"
                  responsive="sm"
                  :busy="loading"
                  show-empty
                  emptyText="Sem dados para mostrar"
                  emptyFilteredText="Nenhum registro encontrado"
                  @row-clicked="(item) => clickBuscar(item)"
                >
                  <template #cell(imagem)="row">
                    <img
                      :src="mostraImagem(row.item.imagem)"
                      class="tamanhoImagem"
                      alt=""
                      srcset=""
                    />
                  </template>
                </b-table>
              </b-overlay>
            </b-card>
          </b-col>
        </b-row>
      </b-container>
    </b-modal>
    <!-- 
            MODAL CONSULTA DE PRODUTOS
         -->
    <b-modal
      id="modalConsultaProdutos"
      v-model="modalConsultaProdutosShow"
      size="lg"
      title="Produtos"
      no-close-on-backdrop
      no-close-on-esc
      header-bg-variant="success"
      ok-only
      ok-variant="info"
      ok-title="Sair"
    >
      <b-container fluid>
        <b-row>
          <b-col>
            <b-card title="Buscar">
              <b-row>
                <b-col sm="11">
                  <b-form-input
                    v-model="searchProdutosModal"
                    type="text"
                    @keypress.enter="buscarProdutosPorNome"
                  ></b-form-input>
                </b-col>
                <b-col sm="1">
                  <b-button
                    pill
                    variant="success"
                    size="sm"
                    @click="buscarProdutosPorNome"
                  >
                    <b-icon-search></b-icon-search>
                  </b-button>
                </b-col>
              </b-row>
            </b-card>
          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <b-card title="Resultados">
              <b-overlay :show="loading" spinner-variant="primary" rounded="sm">
                <b-table
                  stacked="sm" 
                  label-sort-asc=""
                  label-sort-desc=""
                  label-sort-clear=""
                  dense
                  striped
                  hover
                  :items="buscadoProduto"
                  :fields="fieldsProdutos"
                  primary-key="id"
                  small
                  head-variant="light"
                  responsive="xs"
                  :busy="loading"
                  show-empty
                  emptyText="Sem dados para mostrar"
                  emptyFilteredText="Nenhum registro encontrado"
                  @row-clicked="(item) => clickBuscarProduto(item)"
                >
                  <template #cell(imagem)="row">
                    <!-- style="max-width: 40px" estava no img -->
                    <img
                      :src="mostraImagem(row.item.imagem)"
                      alt=""
                      srcset=""
                      class="tamanhoImagem"
                    />
                  </template>
                </b-table>
              </b-overlay>
            </b-card>
          </b-col>
        </b-row>
      </b-container>
    </b-modal>
    <!-- fim consulta produtos -->

    <b-overlay :show="loading" spinner-variant="primary" rounded="sm">
      <template v-slot:overlay>
        <div class="text-center">
          <b-icon icon="stopwatch" font-scale="3" animation="cylon"></b-icon>
          <p id="cancel-label">Aguarde um momento por favor...</p>
        </div>
      </template>

      <!-- 
            BARRA BOTES CONSULTAR VENDA E NOVA VENDA
         -->
      <b-row>
        <b-col>
          <!-- <b-navbar toogleable="lg" variant="default" sticky>
                     <b-navbar-nav>
                         <b-btn-toolbar class="mr-2"> -->
          <b-btn class="mr-2" variant="danger" @click="buscar">
            <b-icon icon="search"></b-icon> Consultar
          </b-btn>
          <!-- </b-btn-toolbar>
                         <b-btn-toolbar> -->
          <b-btn variant="warning" @click="nova">
            <b-icon icon="basket3"></b-icon>Nova Venda
          </b-btn>

          <b-btn variant="warning" @click="abrirModalComprovante" v-if="cabecalho.id > 0">
            <b-icon icon="print"></b-icon>Imprimir
          </b-btn>
          <b-btn variant="warning" @click="abrirModalTicket" v-if="cabecalho.id > 0">
            <b-icon icon="print"></b-icon>Imprimir tique
          </b-btn>
          <!-- </b-btn-toolbar>
                     </b-navbar-nav>
                 </b-navbar> -->
        </b-col>
        <!-- <b-col sm="1">
                 <b-btn-toolbar>
                             <b-btn variant="warning" @click="nova">
                                 <b-icon icon="basket3"></b-icon> Nova
                             </b-btn>
                         </b-btn-toolbar>
             </b-col> -->
      </b-row>

      <!-- 
            CABEALHO DA VENDA
         -->
      <b-row >
        <b-col>
          <b-card  title="Informaes da venda" class="md-2">
            <b-row>
              <!-- <b-col cols="1" sm="1">
                <label for="id">N.:</label>
              </b-col> -->
              <b-col cols="5" md="2">
                <label for="id">Nota:</label>
                <b-form-input
                  v-model="cabecalho.id"
                  :disabled="!editar"
                  type="text"
                ></b-form-input>
              </b-col>
              <!-- <b-col sm="1">
                <label for="data">Data:</label>
              </b-col> -->
              <b-col cols="7" md="2">
                <label for="data">Data:</label>
                <b-form-input
                  v-model="cabecalho.data"
                  type="text"
                  disabled
                ></b-form-input>
              </b-col>
              <!-- <b-col sm="1">
                <label for="cliente">Cliente:</label>
              </b-col> -->
              <b-col cols="5" md="2">
                <!-- <b-form-select v-model="cabecalho.cliente" :options="clientes" value-field="id" text-field="nome"></b-form-select> -->
                
                <!-- <label for="cliente">Cliente:</label>
                <b-form-input
                  v-model="cabecalho.cliente.id"
                  @keypress.enter="buscarCliente"
                  ref="cliente"
                  type="number"
                ></b-form-input> -->

                <b-form-group label="Cliente" label-for="codigoCliente" class="mb-0">
                <b-input-group size="sm">
                  <b-form-input
                    v-model="cabecalho.cliente.id"
                    @keypress.enter="buscarCliente"
                    type="number"
                    id="codigoCliente"
                    placeholder="codigo"
                    ref="cliente"
                  ></b-form-input>
                  <b-input-group-append>
                    <b-button 
                      @click="abrirModal" 
                      variant="success"
                    >
                    <b-icon-people></b-icon-people>
                    </b-button>
                  </b-input-group-append>
                  </b-input-group>
                </b-form-group>

              </b-col>
              <b-col cols="7" md="6">
                <label for="cliente.nome">Nome</label>
                <b-form-input
                  v-model="cabecalho.cliente.nome"
                  disabled
                ></b-form-input>
              </b-col>

              <!-- <b-col cols="2" sm="1">
                <b-button
                  @click="abrirModal"
                  variant="success"
                  :disabled="cabecalho.id != -1"
                  id="btnCliente"
                  mt="3"
                  class="mt-4"
                >
                  <b-icon-people></b-icon-people>
                </b-button>
              </b-col> -->
            </b-row>
          </b-card>
        </b-col>
      </b-row>

      <!-- 
            INFORMAR O PRODUTO 
         -->
 <!--      <b-row v-if="informarProduto">
        <b-col>
  -->         <b-card title="Informaes de produto" class="md-2" v-if="informarProduto">
            <b-row>
              <!-- <b-col cols="3" xs="3" sm="2">
                <label for="idproduto">Cdigo</label>
                <b-form-input
                  v-model="detalhe.produto"
                  @keypress.enter="buscarProduto"
                  ref="idproduto"
                  id="idproduto"
                  type="number"
                ></b-form-input>
              </b-col>
              <b-col cols="2" xs="3" sm="1" lg="1">
                <b-btn
                  class="mt-4"
                  block
                  variant="warning"
                  @keydown.enter="modalConsultaProdutosShow = true"
                  @click="modalConsultaProdutosShow = true"
                >
                  <b-icon icon="binoculars"></b-icon>
                </b-btn>
              </b-col> -->

              <b-col cols="5" md="2">
                <b-form-group label="Cdigo" label-for="idproduto" class="mb-0">
                <b-input-group size="sm">
                  <b-form-input
                    v-model="detalhe.produto"
                    @keypress.enter="buscarProduto"
                    type="number"
                    id="idproduto"
                    placeholder="codigo"
                    ref="idproduto"
                    ></b-form-input>
                  <b-input-group-append>
                    <b-button 
                      @keydown.enter="modalConsultaProdutosShow = true"
                      @click="modalConsultaProdutosShow = true" 
                      variant="success"
                    >
                    <b-icon icon="binoculars"></b-icon>
                    </b-button>
                  </b-input-group-append>
                  </b-input-group>
                </b-form-group>
              </b-col>
              <b-col cols="7" md="5">
                <label for="descricao">Descrio</label>
                <b-form-input
                  v-model="detalhe.descricao"
                  disabled
                ></b-form-input>
              </b-col>
              <b-col cols="5" md="2">
                <label for="quantidade">Quantidade</label>
                <b-form-input
                  v-model="detalhe.quantidade"
                  @keypress.enter="verificaQuantidade"
                  type="number"
                  min="1"
                  value="1"
                  ref="quantidade"
                ></b-form-input>
              </b-col>
              <b-col cols="5" md="2">
                <label for="preco">Preo</label>
                <b-form-input
                  v-model="detalhe.preco"
                  @keypress.enter="verificaPreco"
                  type="number"
                  ref="preco"
                ></b-form-input>
              </b-col>
              <b-col cols="1">
                <b-btn
                  class="mt-4"
                  block
                  variant="info"
                  ref="addItemGrade"
                  @keydown.enter="addItemGrade"
                  @click="addItemGrade"
                >
                  <!-- <b-icon icon="cart-plus" @click="save"></b-icon> -->
                  <b-icon icon="cart-plus"></b-icon>
                </b-btn>
              </b-col>
            </b-row>
          </b-card>
        <!-- </b-col>
      </b-row> -->

      <!--********************************************************************************************* 
      TABELA DOS ITENS DA VENDA
      **********************************************************************************************-->
      <!-- <b-row v-if="itens.length">
        <b-col> -->
          <b-card  title="Produtos da venda" class="md-2" v-if="itens.length">
            <b-row>
              <b-table
                stacked="sm"
                label-sort-asc=""
                label-sort-desc=""
                label-sort-clear=""
                dense
                striped
                hover
                :items="itens"
                :fields="fields"
                primary-key="id"
                small
                sticky-header
                head-variant="dark"
                responsive="sm"
                :busy="loading"
                show-empty
                emptyText="No existe dados cadastrados"
                emptyFilteredText="Nenhum registro encontrado"
                foot-clone
              >
                <template v-slot:cell(acoes)="row">
                  <b-icon
                    icon="trash"
                    size="sm"
                    @click="apagarDetalhe(row)"
                    class="mr-1"
                  ></b-icon>
                </template>

                <template v-slot:foot()>
                  <span></span>
                </template>

                <template v-slot:foot(produto_descricao)>
                  <span class="text-danger">Totais</span>
                </template>

                <template v-slot:foot(quantidade)>
                  <span class="text-danger">{{ totais.quantidade }}</span>
                </template>

                <template v-slot:foot(subtotal)>
                  <span class="text-danger">{{ totais.subtotal }}</span>
                </template>

                <template v-slot:foot(desconto)>
                  <span class="text-danger">{{ totais.desconto }}</span>
                </template>

                <template v-slot:foot(total)>
                  <span class="text-danger">{{ totais.total }}</span>
                </template>
              </b-table>
            </b-row>
          </b-card>
        <!-- </b-col>
      </b-row> -->

      <b-card v-if="itens.length">
        <b-row>
          <b-col sm="4" md="4" >
            <span>Total:</span><span> {{ totais.total*5800 | guarani }} </span>
          </b-col>
          <b-col sm="4" md="4" >
            <span>Total:</span><span> {{ totais.total*5.8 | real }} </span>
          </b-col>
          <b-col sm="4" md="4" class="ml-auto">
            <!-- <h2 class="text-sm-end d-none d-sm-block"> -->
              <h2 class="text-sm-end">
              <span>Total:</span><span> {{ totais.total | dolar }} </span>
            </h2>

          </b-col>
        </b-row>
      </b-card>
      
      <!-- Rodap da venda -->

