 -->

      <b-row>
        <b-col xs="6">
          <div class="d-grid gap-2">
            <b-button type="button" class="btn btn-success btn-lg btn-block" 
              ref="save" 
              @click="save"
              :disabled="!itens.length"
              >
            <b-icon icon="check-square-fill"></b-icon> Gravar
            </b-button>
          </div>
        </b-col>
        <b-col xs="6">
          <div class="d-grid gap-2">
            <button type="button" class="btn btn-warning btn-lg btn-block"  @click="voltar">
              <b-icon icon="arrow-left"></b-icon> Voltar
            </button>
          </div>
        </b-col>
      </b-row>


<div>
  
</div>
    </b-overlay>



    <!-- impresso -->
    <b-modal 
    v-model="modalComprovanteShow" 
    size="lg"
    ok-only
    ok-variant="info"
    ok-title="Sair"
    >
        <b-card>
          <h5 class="noPrint">Comprovante de venda</h5>
          <div class="noPrint">
            <b-btn @click="criarPDF()"><v-icon>print</v-icon></b-btn> Imprimir
            <b-btn @click="criarPDF2()"><v-icon>print</v-icon></b-btn> Tiket
          </div>
        <b-card-text>
          <div id="factura">
            <header>
              <div id="logo">
                <img
                  src="data:image/png;base64,"
                  id="imagen"
                />
              </div>
              <div id="datos">
                <p id="encabezado">
                  <b>Infinity Group S.A.</b><br />Av. San Bls, 136 - Ciudad del
                  Este, Paraguay<br />Telefono:(+595)512216<br />Email:infinity@gmail.com
                </p>
              </div>
              <div id="fact">
                <p>
                  Venda N: {{cabecalho.id}} <br>
                  Data: {{cabecalho.data}}
                  <!-- {{ tipo_comprobante }}<br />
                  {{ serie_comprobante }}-{{ num_comprobante }}<br />
                  {{ fecha }} -->
                </p>
              </div>
            </header>
            <br />
            <section>
              <div>
                <table id="facliente">
                  <tbody>
                    <tr>
                      <td id="cliente">
                        <strong>Sr(a). {{ cabecalho.cliente.nome }}</strong
                        ><br />
                        <strong>Documento: {{ cabecalho.cliente.nome }}</strong> <br />
                        <strong>Endereo: Rua das Amlias</strong> <br />
                        <strong>Telefone: {{ cabecalho.cliente.telefone }}</strong><br />
                        <strong>Email: {{ cabecalho.cliente.email }}</strong>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </section>
            <br />
            <section>
              <div>
                <table id="facarticulo">
                  <thead>
                    <tr id="fa">
                      <th>QUANT.</th>
                      <th>DESCRIO</th>
                      <th>PREO</th>
                      <th>DESC.</th>
                      <th>TOTAL</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="det in itens" :key="det._id">
                      <td style="text-align: center">{{det.quantidade}}</td>
                      <td>{{ det.produto_descricao }}</td>
                      <td style="text-align: right">{{ det.preco.toFixed(2) }}</td>
                      <td style="text-align: right">{{ det.desconto.toFixed(2) }}</td>
                      <td style="text-align: right">
                        {{ (det.quantidade * det.preco - det.desconto).toFixed(2) }}
                      </td>
                    </tr>
                  </tbody>
                  <tfoot>
                    <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th style="text-align: right">SUBTOTAL</th>
                      <th style="text-align: right">
                        $ {{totais.subtotal.toFixed(2)}}
                      </th>
                    </tr>
                    <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th style="text-align: right">IVA({{ 0 }}%)</th>
                      <th style="text-align: right">
                        $ 0.00
                      </th>
                    </tr>
                    <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th style="text-align: right">TOTAL</th>
                      <th style="text-align: right">
                        $ {{totais.total.toFixed(2)}}
                      </th>
                    </tr>
                  </tfoot>
                </table>
                
              </div>
            </section>
            <br />
            <br />
            <footer>
              <div id="gracias">
                <p><b>Obrigado pela compra!</b></p>
              </div>
            </footer>
          </div>
        </b-card-text>
        <!-- <b-btn @click="fecharModalComprovante()">Fechar tela</b-btn> -->
      </b-card>
      
    </b-modal>
    <!-- fimimpresso -->



