            <section>
             <div class="negrito" >
               <span>ID</span>
               <span style="margin-left: 30px;">DESCRIO</span><br>
               <span>QTD'</span>
               <span style="margin-left: 15px;">PREO</span>
               <span style="margin-left: 15px;">DESC.</span>
               <span style="margin-left: 15px;">TOTAL</span>
             </div>
             <br>
             <div id="ticketProdutos" v-for="det in itens" :key="det._id">
