                    $ {{totais.subtotal.toFixed(2)}}
                </span>
                <br>
                <span style="text-align: right">IVA({{ 0 }}%)</span>
                <span style="text-align: right">$ 0.00</span><br>
                <span style="text-align: right">TOTAL</span>
                <span style="text-align: right">$ {{totais.total.toFixed(2)}}</span>
            </section> 
            
            <footer>
              <div id="gracias">
                <p><b>Obrigado!</b></p>
