    <!-- lista de vendas -->
    <div id="listagem" v-if="showList">
      <!-- <PageHeader :title="title" :items="items" /> -->
       <v-app>
        <v-row>
            <v-col>
                <v-text-field v-model="search" append-icon="mdi-magnify" label="Buscar"></v-text-field>
                <v-row>
                                  <v-col cols="12" md="6">
                  <div class="mb-3">
                    <label>Data Inicial</label>
                    <br />
                    <date-picker v-model="dataInicial" :first-day-of-week="1" lang="pt-br" format="DD/MM/YYYY"></date-picker>
                  </div>
                </v-col>
                <v-col cols="12" md="6">
                    <div class="mb-3">
                      <label>Data Final</label>
                      <br />
                      <date-picker v-model="dataFinal" :first-day-of-week="1" lang="pt-br" format="DD/MM/YYYY"></date-picker>
                    </div>
                </v-col>

                </v-row>                

                <v-data-table
                    dense
                    :headers="headers"
                    :items="itensLista"
                    :search="search"
                    class="elevation-1"
                    :loading="loading"
                    loading-text="Carregando..."
                >

                    <template v-slot:top>
                        <v-toolbar flat color="white">
                            <v-toolbar-title>Vendas</v-toolbar-title>
                            <v-divider class="mx-4" inset vertical></v-divider>
                            <v-spacer></v-spacer>
                            <v-btn class="mb-2" color="warning" dark @click="iniciarLista"><v-icon>cached</v-icon></v-btn>
                            <v-btn class="mb-2" color="success" dark @click="novaVenda"><v-icon>add_box</v-icon></v-btn>
                            
                            <v-dialog  max-width="500px">
                                <template v-slot:activator="{ on, attrs }">
                                    <!-- <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
                                        <v-icon>add_box</v-icon>
                                    </v-btn> -->
                                </template>
                                <v-card>
                                    <v-card-title>
                                        <!-- <span class="headline">{{ formTitle }}</span> -->
                                    </v-card-title>
                                    <v-card-text>
                                        <v-container>
                                            <v-row>
                                                <v-col cols="2" sm="2" md="2">
                                                    <v-text-field v-model="editedItem.id" label="ID" disabled></v-text-field>
                                                </v-col>
                                                <v-col cols="10" sm="10" md="10">
                                                    <v-text-field v-model="editedItem.descricao" label="Descrio"></v-text-field>
                                                </v-col>
                                            </v-row>
                                        </v-container>
                                    </v-card-text>
                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                                        <v-btn color="pink accent-3" text @click="save">Salvar</v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-dialog>

                        </v-toolbar>                        
                    </template>
                    
                    <template v-slot:item.total="{ item }">
                      $ {{item.total.toFixed(2)}}
                    </template>

                    <template v-slot:item.acoes="{ item }">
                        <v-icon small color="green darken-2" class="mr-2" @click="editItem(item)">mdi-eye</v-icon>
                        <v-icon small color="red darken-2" @click="deleteItem(item)">mdi-delete</v-icon>
                    </template>

                    <template v-slot:no-data>
                        <v-btn color="primary" @click="iniciar">Reiniciar</v-btn>
                    </template>
                </v-data-table>
                <v-card>
                  <v-card-text>
                    <h2 class="text-sm-end">
                      <span>Total:</span><span> {{ totaisConsulta | dolar }} </span>
                    </h2>
                  </v-card-text>
                </v-card>
            </v-col>
        </v-row>
    </v-app>
