</template>




<script>
import Layout from "../../../layouts/main";
import PageHeader from "@/components/page-header";

import { ApiFac } from "./ApiFac";
import { ApiInv } from "../inv/ApiInv";
import mensagemMixin from "@/mixins/mensagensMixin.js";
import moment from "moment";
import jsPDF from 'jspdf'
import html2canvas from 'html2canvas'
export default {
  name: "Faturar",
  components: {
    Layout,
    PageHeader,
  },
  mixins: [mensagemMixin],
  props: [],
  data() {
    return {
      title: "Venda",
      items: [
        {
          text: "Dashboard",
          href: "/",
        },
        {
          text: "Venda",
          active: true,
        },
      ],
      showList:true,
      informarProduto:false,
      empresa:{},
      img_url: null,
      searchModal: "",
      modalShow: false,
      modalConsultaProdutosShow: false,
      modalComprovanteShow: false,
      modalTicketShow:false, //mostra modal do ticket
      modalTiqueShow:false,
      buscado: [],
      buscadoProduto: [],
      api: new ApiFac(),
      apiInv: new ApiInv(),
      editar: false,
      loading: false,
      search: "",
      searchProdutosModal: "",
      clientes: [],
      cabecalho: {
        id: -1,
        cliente: {
          id: -1,
          nome: "",
        },
        empresa: {
          id: -1,
          nome: "",
        },
        //data: new Date().toLocaleString(),
        data: moment().format("DD/MM/YYYY"),
        total:0,
      },
      detalhe: {
        id: -1,
        venda: -1,
        produto: -1,
        descricao: "",
        quantidade: 1,
        preco: 0,
        subtotal: 0,
        desconto: 0,
        total: 0,
      },
      itens: [],
      produtosVenda: [
        //{id: -1, venda:-1, produto:5,quantidade:1, preco:10, desconto:0, subtotal:100, total:1000},
        //{id: -1, venda:-1, produto:6,quantidade:2, preco:20, desconto:0, subtotal:200, total:2000},
        //{id: -1, venda:-1, produto:7,quantidade:3, preco:30, desconto:0, subtotal:300, total:3000}
      ],
      fields: [
        { key: "id", label: "ID", sortable: true },
        { key: "produto_descricao", label: "Produto", sortable: true },
        { key: "quantidade", label: "Quantidade", sortable: true },
        { key: "preco", label: "Preo", sortable: true },
        { key: "subtotal", label: "subtotal", sortable: true, formatter:(value) =>{return value.toFixed(2)} },
        { key: "desconto", label: "desconto", sortable: true },
        { key: "total", label: "total", sortable: true, formatter:(value) =>{return value.toFixed(2)} },
        { key: "acoes", label: "Aes" },
      ],
      fieldsProdutos: [
        { key: "id", label: "ID", sortable: true },
        { key: "descricao", label: "Descrio", sortable: true },
        { key: "stock", label: "Estoque", sortable: true },
        { key: "preco", label: "Preo", sortable: true },
        { key: "subcat_descricao", label: "Sub Categoria", sortable: false },
        { key: "imagem", label: "Foto", sortable: false },
      ],
      //listagem das vendas
      itensLista:[],
      //search: "",
            headers:[
                {text: "ID", value: "id"},
                {text: "Data", align: 'start', sortable: false, value: 'data'},
                {text: "Total da Venda", align: 'end', sortable: false, value: 'total'},
                {text: "Cliente", align: 'start', sortable: false, value: 'cliente.nome'},
                {text: "Empresa", align: 'start', sortable: false, value: 'empresa'},
                {text: "Aes", value:'acoes', sortable:false}
            ],
            dialog:false,
            editedIndex:-1,
            editedItem:{ id:-1, descricao:""},
            defaultItem:{ id:-1, descricao:""},
            dataInicial: new Date(), //o componente datepicker espera um tipo Date()
            dataFinal: new Date(),
    };
  },
  created() {
    /* desabilitado estava dando erro de focus depois que foi aterado para mostrar listagem de vendas */
    //this.iniciar();
    this.iniciarLista()
  },
  mounted() {
    if((localStorage.getItem("empresa") !== 'undefined') && (localStorage.getItem("empresa") !== null)){
      this.empresa = JSON.parse(localStorage.getItem("empresa"))
    } else {
      this.mensagem("Selecione uma empresa!", "Ateno", "warning") //mixin
      this.empresa = {}
      this.$router.push('/empresas')
    }
  },
  methods: {
    mostraImagem(obj) {
      console.log(obj);
      let result = "";
      if (obj) {
        result = this.img_url + obj;
      } else {
        result = this.img_url + "media/img/empty.png";
      }
      return result;
    },
    async iniciar() {
      try {
        this.loading = true;
        const clientes = await this.api.getCliente();
        this.img_url = this.api.IMG_URL;
        this.clientes = clientes;
        this.nova();
        this.$refs.cliente.focus();
        this.$refs.cliente.select();
        this.title = "Venda"
        this.loading = false;
        this.informarProduto = true
      } catch (error) {
        this.$swal("Error", error.toString());
      } finally {
        this.loading = false;
      }
    },
    async iniciarLista(){
           try {
                let dataInicial = moment(this.dataInicial, "DD/MM/YYYY").format("YYYY-MM-DD");
                let dataFinal = moment(this.dataFinal, "DD/MM/YYYY").format("YYYY-MM-DD");
                let empresa = this.empresa.id
                if(dataInicial == 'Invalid date' || dataFinal == 'Invalid date'){
                    dataInicial = ""
                    dataFinal = ""
                    this.dataInicial = ""
                    this.dataFinal = ""
                }
                this.loading = true
                let vendas = await this.api.getVenda(-1, dataInicial, dataFinal, empresa)
                /* se o servidor responder um detail provavelmnete o token espirou */
                if(vendas.detail){
                  this.$swal({title: 'Ateno!', text:vendas.detail, icon: 'warning'})  
                  this.$router.push('login')
                }
                /* se voltar uma string  porque o servidor respondeu uma mensagem de erro */
                if(typeof(vendas)=='string'){
                  this.mensagemErro(vendas)
                  this.itensLista = []
                  return false
                }
                this.itensLista = vendas
                this.title = "Lista de vendas"
           } catch (error) {
                //this.mensagem(error, "Houve um erro!", "error") //mixin local
                this.mensagemErro(error) //mixin global no main.js
           } finally{
               this.loading = false
           }
       },
    novaVenda(){
      this.showList = false
      this.informarProduto = true
      this.iniciar()
    },
    async voltar(){
        //this.$router.push('/venda')

        if(await this.mensagemPergunta('Sair dessa venda?', 'Confirme!')){
          this.showList = true
          this.iniciarLista()
        }
    },
    async editItem(item){
      //this.informarProduto = false
      //this.novaVenda()
      this.showList = false
      //await this.ocultarProduto()
      this.cabecalho.id = item.id
      await this.refresh();
      if (this.cabecalho.id === undefined) {
        this.limpaCabecalho();
         this.$swal("Fatura no encontrada", item.id, "error");
      }
      //this.informarProduto = false
      await this.ocultarProduto()
    },
    async ocultarProduto(){
      this.informarProduto = false
    },
    close(){
           this.dialog = false
           //this.$nextTick(()=>{
           //    this.editedItem = Object.assign({}, this.defaultItem)
           //    this.editedIndex = -1
           //})
    },
    async buscarCliente() {
      try {
        this.loading = true;
        const cliente = await this.api.getCliente(this.cabecalho.cliente.id);
        if (cliente.detail != undefined) {
          this.$swal("Erro", cliente.detail);
          this.cabecalho.cliente = { id: -1 };
        } else if (!cliente.estado) {
          this.$swal("Erro", "Cliente inativo");
          this.cabecalho.cliente = { id: -1 };
        } else {
          this.cabecalho.cliente = cliente;
          //this.$refs.idproduto.focus()
          //this.$refs.idproduto.select()
          this.focarProduto();
        }
        this.loading = false;
      } catch (error) {
        this.$swal("Erro", error.toString());
      } finally {
        this.loading = false;
      }
      /* const d = await this.api.getClienteByName('ggg')
            console.log('....... ', d) */
    },
    async buscarClientePorNome() {
      try {
        this.loading = true;
        this.buscado = [];
        console.log(this.searchModal);
        if (this.searchModal != "") {
          const d = await this.api.getClienteByName(this.searchModal);
          console.log(d);
          if (d.detail != undefined) {
            this.$swal("Erro", d.detail);
          } else {
            this.buscado = d;
          }
        }
        this.loading = false;
      } catch (error) {
        this.$swal("Erro", error.toString());
      } finally {
        this.loading = false;
      }
      /* const d = await this.api.getClienteByName('ggg')
            console.log('....... ', d) */
    },
    async buscarProdutosPorNome() {
      try {
        this.loading = true;
        this.buscadoProduto = [];

        if (this.searchProdutosModal != "") {
          const produtos = await this.api.getProdutoByName(
            this.searchProdutosModal
          );
          if (produtos.detail != undefined) {
            this.$$refs.idproduto.select()
            this.$swal("Erro:", produtos.detail);
          } else {
            this.buscadoProduto = produtos;
          }
        }
      } catch (error) {
        this.$swal("Erro", error.toString());
      } finally {
        this.loading = false;
      }
    },
    async clickBuscar(item) {
      await this.fecharmodalConsultaClientes();
      this.cabecalho.cliente.id = item.id;
      await this.buscarCliente();
    },
    async clickBuscarProduto(item) {
      await this.fecharmodalConsultaProdutos();
      this.detalhe.produto = item.id;
      await this.buscarProduto();
    },
    abrirModal() {
      this.buscado = [];
      this.searchModal = "";
      this.modalShow = true;
    },
    async buscarProduto() {
      if (this.detalhe.produto == "") {
        this.modalConsultaProdutosShow = true;
      }
      if (this.detalhe.produto > 0) {
        try {
          this.loading = true;
          const p = await this.api.getProdutos(this.detalhe.produto);
          if (p.id !== undefined) {
            if (p.stock > 0) {
              this.detalhe.produto = p.id;
              this.detalhe.descricao = p.descricao;
              this.detalhe.preco = p.preco;
              this.$refs.quantidade.focus();
              this.$refs.quantidade.select();
            } else {
              //await this.mensagemErro(`Quantidade insuficiente ${p.stock}`)
              this.$toastr.w(`Quantidade insuficiente ${p.stock}`);
              await this.limparDetalhe();
              await this.focarProduto();
            }
          } else {
            //this.$refs.idproduto.select()
            this.focarProduto()
            this.mensagemErro(p.detail);
            this.limparDetalhe();
          }
          this.loading = false;
        } catch (error) {
          this.$swal("Erro", error.toString());
        } finally {
          this.loading = false;
        }
      }
    },
    async save_ok() {
      let dataFatura = this.cabecalho.data;
      dataFatura = moment(dataFatura, "DD/MM/YYYY").format("YYYY-MM-DD");
      if (
        this.cabecalho.cliente.id === undefined ||
        this.cabecalho.cliente.id === null ||
        this.cabecalho.cliente.nome == "" ||
        this.cabecalho.cliente.id < 1
      ) {
        this.mensagemErro("Cliente  obrigatrio!");
        return false;
      }
      if (
        this.detalhe.descricao === undefined ||
        this.detalhe.descricao == ""
      ) {
        this.mensagemErro("Produto  obrigatrio!");
        return false;
      }
      if (
        this.detalhe.quantidade === undefined ||
        this.detalhe.quantidade <= 0
      ) {
        this.mensagemErro("Verifique a quantidade informada");
        return false;
      }

      try {
        this.loading = true;
        const enc = {
          id: this.cabecalho.id,
          cliente: this.cabecalho.cliente.id,
          data: dataFatura,
        };

        const obj = new FormData();
        for (const key in enc) {
          obj.append(key, enc[key]);
        }

        const f = await this.api.saveVenda(obj);
        if (f.id === undefined) {
          this.mensagemErro(`Falha ao gravar o cabealho da venda: ${f} `);
        } else {
          let id = f.id;
          const det = {
            id: -1,
            venda: id,
            produto: this.detalhe.produto,
            quantidade: this.detalhe.quantidade,
            preco: this.detalhe.preco,
            desconto: this.detalhe.desconto,
          };
          console.log("det ", det);
          console.log("f ", f);
          const objDet = new FormData();
          for (const key in det) {
            objDet.append(key, det[key]);
          }

          try {
            const d = await this.api.saveDetalheVenda(objDet);
            if (d.id === undefined) {
              this.mensagemErro(`Falha ao gravar o detalhe da venda: ${d} `);
              this.cabecalho.id = f.id;
              this.refresh();
            } else {
              //this.detalhe = {id:-1}
              this.limparDetalhe();
              this.cabecalho = f;
              this.cabecalho.cliente = await this.api.getCliente(
                this.cabecalho.cliente
              );
              this.cabecalho.data = moment(dataFatura, "YYYY-MM-DD").format(
                "DD/MM/YYYY"
              );
            }
          } catch (error) {
            this.$swal("Erro det", error);
          }
        }
      } catch (error) {
        this.$swal("Erro Venda", error);
      } finally {
        this.loading = false;
        this.refresh();
      }
    },
    async save() {
      let dataFatura = this.cabecalho.data;
      dataFatura = moment(dataFatura, "DD/MM/YYYY").format("YYYY-MM-DD");
      if (
        this.cabecalho.cliente.id === undefined ||
        this.cabecalho.cliente.id === null ||
        this.cabecalho.cliente.nome == "" ||
        this.cabecalho.cliente.id < 1
      ) {
        this.mensagemErro("Cliente  obrigatrio!");
        return false;
      }
      /* if(this.detalhe.descricao ===undefined
                || this.detalhe.descricao ==""
            ){
                this.mensagemErro("Produto  obrigatrio!")
                return false
            } */
      if (this.itens.length <= 0) {
        this.$refs.idproduto.focus();
        this.$refs.idproduto.select();
        this.mensagemErro("Produto  obrigatrio!");
        return false;
      }

      try {
        this.loading = true;
        const enc = {
          id: this.cabecalho.id,
          cliente: this.cabecalho.cliente.id,
          empresa: this.empresa.id,
          data: dataFatura,
          total:this.totais.total,
          produtos: JSON.stringify(this.itens),
        };

        console.clear();
        console.log(enc);
        //return false

        const obj = new FormData();
        for (const key in enc) {
          obj.append(key, enc[key]);
        }
        ////obj.append('produtos', JSON.stringify(this.produtosVenda))
        //obj.append('produtos', JSON.stringify(this.itens))

        const venda = await this.api.saveVenda(obj);

        if (venda.id === undefined) {
          this.loading = false
          this.mensagemErro(`Falha ao gravar o cabealho da venda: ${venda} `);
          return false
          //throw new Error('Erro ao gravar o cabealho da venda')
        } else {
          //

          this.cabecalho.id = venda.id;
          await this.refresh();
          this.$toastr.s(`Venda gravada ${venda.id}`);
          //this.$router.push('/vendas')
          this.showList = true
          this.iniciarLista()
          
          
          
          
          //this.limparDetalhe()
          //

          /* 
                    let id = f.id
                    const det = {
                        id:-1,
                        venda: id,
                        produto: this.detalhe.produto,
                        quantidade: this.detalhe.quantidade,
                        preco: this.detalhe.preco,
                        desconto: this.detalhe.desconto
                    }
                    console.log('det ',det)
                    console.log('f ',f)
                    const objDet = new FormData()
                    for (const key in det) {
                        objDet.append(key, det[key])
                    }

                    try {
                        const d = await this.api.saveDetalheVenda(objDet)
                        if(d.id===undefined){
                            this.mensagemErro(`Falha ao gravar o detalhe da venda: ${d} `)
                            this.cabecalho.id = f.id
                            this.refresh()
                        } else {
                            //this.detalhe = {id:-1}
                            this.limparDetalhe()
                            this.cabecalho = f
                            this.cabecalho.cliente = await this.api.getCliente(this.cabecalho.cliente)
                            this.cabecalho.data = moment(dataFatura, 'YYYY-MM-DD').format('DD/MM/YYYY')
                        }
                        
                    } catch (error) {
                        this.$swal("Erro det", error) 
                    }
                     */
        }
      } catch (error) {
        //this.$swal("Erro Venda", error)
        //this.mensagemErro(`Falha ao gravar venda: `, error.toString());
        this.mensagemErro(`Falha ao gravar venda: `, error.message);
      } finally {
        this.loading = false;
        this.refresh();
      }
    },
    async refresh() {
      try {
        this.loading = true;
        const r = await this.api.getVenda(this.cabecalho.id);
        console.log('***********************************************')
        if (r.detail != undefined) {
          this.mensagemErro(r.detail);
          //this.cabecalho = {id:-1, cliente:{id:-1, nome:""},data: moment().format("DD/MM/YYYY")}
          this.nova();
        } else {
          this.cabecalho = r;
          this.cabecalho.cliente = await this.api.getCliente(this.cabecalho.cliente);
          this.cabecalho.empresa = await this.apiInv.getEmpresas(this.cabecalho.empresa)
          this.cabecalho.data = moment(r.data, "YYYY-MM-DD").format(
            "DD/MM/YYYY"
          );
          this.itens = r.detalhe;
          console.log("CONSULTANDO A VENDA");
          console.log("ITENS:");
          console.log(this.itens);
        }
      } catch (error) {
        this.mensagemErro(`Erro ao refrescar a venda: ${error}`);
      } finally {
        this.loading = false;
      }
    },
    async focarProduto() {
      /* Depois de sair de um modal no funciona o focus
               tive que usar o setTimeout e colocar o fechamento do modal 
               em uma funo async await
             */
      setTimeout(() => {
        this.$refs.idproduto.focus();
        this.$refs.idproduto.select();
      }, 100);
    },
    async focarCliente() {
      /* Depois de sair de um modal no funciona o focus */
      setTimeout(() => {
        this.$refs.cliente.focus();
        this.$refs.cliente.select();
      }, 100);
    },
    async fecharmodalConsultaProdutos() {
      this.modalConsultaProdutosShow = false;
    },
    async fecharmodalConsultaClientes() {
      this.modalShow = false;
    },
    async limparDetalhe() {
      this.detalhe = {
        id: -1,
        venda: -1,
        produto: -1,
        quantidade: 1,
        preco: 0,
        subtotal: 0,
        desconto: 0,
        total: 0,
      };
    },
    limpaCabecalho() {
      this.cabecalho = {
        id: -1,
        cliente: { id: -1, nome: "" },
        data: moment().format("DD/MM/YYYY"),
      };
    },
    verificaQuantidade() {
      this.$refs.preco.focus();
      this.$refs.preco.select();
    },
    verificaPreco() {
      this.$refs.addItemGrade.focus();
    },
    async buscar() {
      const { value: idEnc } = await this.$swal.fire({
        title: "Digite o nmero da venda",
        input: "text",
        allowOutsideClick: false,
        showCancelButton: true,
        inputValidator: (value) => {
          if (!value) {
            return "Deve digitar o numero da fatura";
          }
        },
      });

      if (idEnc) {
        this.cabecalho.id = idEnc;
        await this.refresh();
        if (this.cabecalho.id === undefined) {
          this.limpaCabecalho();
          this.$swal("Fatura no encontrada", idEnc, "error");
        }
      } else {
        this.$swal("Busca cancelada", "", "warning");
      }
    },
    nova() {
      this.limpaCabecalho();
      this.limparDetalhe();
      this.editar = false;
      this.itens = [];
      this.informarProduto = true
    },
    async apagarDetalhe(item) {
      console.log(item);
      if (
        await this.mensagemPergunta(
          `${item.index + 1} ${item.item.produto_descricao}?`,
          "Apagar o item"
        )
      ) {
        //await this.api.deleteDetalhe(item.id)
        //this.refresh()
        if (this.cabecalho.id == -1) {
          this.itens.splice(item.index, 1);
        } else {
          this.$toastr.e(
            `No  permitido remover item de uma venda realizada ${this.cabecalho.id}`
          );
        }
      }
    },
    async addItemGrade() {
      if(this.detalhe.produto <1){
        this.$refs.idproduto.focus()
        this.mensagemErro("Informe o produto");
        return false;
      }
      if(this.detalhe.preco <=0){
        this.$refs.preco.focus()
        this.mensagemErro("Informe o preo");
        return false;
      }
      if (
        this.detalhe.quantidade === undefined ||
        this.detalhe.quantidade <= 0
      ) {
        this.$refs.quantidade.select();
        this.mensagemErro("Verifique a quantidade informada");
        return false;
      }

      let aux = {};
      aux.id = this.detalhe.produto;
      aux.produto = this.detalhe.produto;
      aux.produto_descricao = this.detalhe.descricao;
      aux.quantidade = this.detalhe.quantidade;
      aux.preco = parseFloat(this.detalhe.preco); //estava dando erro de det.parse
      //aux.subtotal = (parseInt(aux.quantidade) * parseFloat(aux.preco)).toFixed(2);
      aux.subtotal = aux.quantidade * aux.preco
      aux.desconto = 0;
      aux.total = aux.subtotal - aux.desconto;
      this.itens.push(aux);
      await this.limparDetalhe();

      this.$refs.idproduto.focus();
      this.$refs.idproduto.select();
      console.log(this.itens);
    },
    abrirModalComprovante() {
      this.modalComprovanteShow = true
    },
    fecharModalComprovante() {
      this.modalComprovanteShow = false
    },
    abrirModalTicket() {
      this.modalTicketShow = true
    },
    imprimirTicket(){
      window.print()
    },
    fecharModalTique() {
      this.modalTiqueShow = false
    },
    criarPDF(){
      var quotes = document.getElementById('factura');
      html2canvas(quotes).then(function (canvas) {
        var imgData = canvas.toDataURL('image/png');
        var imgWidth = 210;
        var pageHeight = 295;
        
        var imgHeight = canvas.height * imgWidth / canvas.width;
        var heightLeft = imgHeight;

        //var doc = new jsPDF('p', 'mm', [80, 400]);
        var doc = new jsPDF('p', 'mm', 'a4');
        var position = 0;

        doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight
          doc.addPage()
          doc.addImage(imgData, 'PNG',0, position, imgWidth, imgHeight)
          heightLeft -= pageHeight
        }
        doc.autoPrint({variant: 'non-conform'});
        doc.save('ComprovanteVenda.pdf')
      })
    },
    criarPDF2(){
      var doc = new jsPDF('p', 'mm', [80, 160]);

      doc.text("VENDA", 20, 20);
      let linha = 30
      doc.setFont("courier", "normal");
      doc.setFontSize(10);
      for (let i = 0; i < this.itens.length; i++) {
        doc.text(this.itens[i].produto_descricao, 20, linha);
        linha = linha + 5
      }
      doc.save('TiketVenda.pdf')
    },
  },
  computed: {
    totais() {
      let t = {
        quantidade: 0,
        subtotal: 0,
        desconto: 0,
        total: 0,
      };
      if (this.itens != undefined) {
        this.itens.reduce((i, obj) => {
          t.quantidade += obj.quantidade;
          t.subtotal += obj.subtotal;
          t.desconto += obj.desconto;
          t.total += obj.total;
        }, 0);
      }
      return t;
    },
    totaisConsulta(){
      let t = 0
            this.itensLista.map(x=>{
                t = t + x.total
            })
            return t
    }
  },
};
</script>

